import React, { useState } from 'react';
import logo from './logo.svg';
import './App.css';
import MenuBar from './components/MenuBar/MenuBar'
import 'bootstrap/dist/css/bootstrap.min.css';
import Footer from './components/Footer/Footer';
import YourHabits from './components/YourHabits/YourHabits';
import NewHabit from './components/NewHabit/NewHabit';
import { get, set } from 'idb-keyval';
export const ContentContext = React.createContext();

function App() {


  

  const [page, setPage] = useState(0);

  var content = <YourHabits />;
  if (page === 0) {
      content = <YourHabits />;
  } else if (page === 1){
    content = <NewHabit />;
  } else if (page === 2){

  } 

  let setNewContext = (value) => {
    setPage(value)
  }
  
  return (
    <ContentContext.Provider value={{setNewContext, setDB, getDB}}>
    <div className="App">
        <MenuBar />
        {content}
        <Footer props={page}/>
    </div>
    </ContentContext.Provider>
  );
}

async function getDB() {
      return await get(`habit${1}`)
}

async function setDB(data) {
      await set(`habit${1}`, data);
}

export default App;
